body{
    margin: 0;
    padding: 0;
}
*{
    padding: 0;
    margin: 0;
    font-family: 'Inter', sans-serif;

}
.rf--header{
    display: flex;
    align-items: center;
    width: 50%;
    height: 90px;
    justify-content: space-between;
    background-color: #21222A;
    margin: auto;
    /* padding: 0px 30px; */
    margin-top: 40px;
}
.rf--logo--image{
    width: 28px;
    margin-right: 10px;
    padding-left: 30px;
    
    
}
.rf--logo--text{
    color: #61DAFB;
    font-size: 22px;
    font-weight: 700;
    padding-right: 10px;
}

.rf--logo{
    display: flex;
    align-items: center;


}

.rf-title{
    color: #DEEBF8;
    font-size: 16px;
    padding-right: 30px;

}
.facts{
    background-color: #282D35;
    width: 50%;
    margin: auto;
}
.facts--heading{
    padding: 57px 27px 0px 27px;
    color: #FFFFFF;
    font-size: 40px;
}
.facts--points{
    padding: 30px 74px;
    color: #FFFFFF;

}
.facts--points>li{
    padding: 0px 0px 15px 0px;
    position: relative;

}
.facts--points>li::marker{
    color: #61DAFB;
    font-size: 1.4rem;
}